// =============================================================================
// SMF PUBLIC SPEC
// =============================================================================
// Machine-readable specification for SMF (Smart Meta-Facilitator) public APIs
// This spec describes the public endpoints available for external integrators
//
// Example usage:
//   curl https://api.nexflowapp.app/api/smf/spec
//
// =============================================================================

/**
 * SMF Public API Specification (OpenAPI 3.0 compatible)
 * 
 * This document describes the public-facing SMF endpoints that external
 * clients and the nexflow-web app can consume.
 */
export const SMF_SPEC = {
  openapi: '3.0.3',
  info: {
    title: 'NexFlow SMF Public API',
    description: `
Smart Meta-Facilitator (SMF) public API for x402 payment routing insights.

SMF aggregates data from multiple sources:
- **x402scan**: Observability metrics (success rate, latency, error rate)
- **Scattering/Dune**: Activity metrics (volume, transactions, unique users)
- **NexFlow internal**: Our own routing telemetry (dogfood, scout, external)

These endpoints are read-only and designed for:
- Dashboard and analytics integrations
- Agent clients making routing decisions
- Design tooling (Figma, etc.) that needs real data shapes
    `.trim(),
    version: '1.0.0',
    contact: {
      name: 'NexFlow Team',
      url: 'https://nexflowapp.app',
    },
  },
  servers: [
    {
      url: 'https://api.nexflowapp.app',
      description: 'Production API',
    },
    {
      url: 'https://www.nexflowapp.app',
      description: 'Production (www)',
    },
  ],
  paths: {
    // =========================================================================
    // PUBLIC SMF OVERVIEW
    // =========================================================================
    '/api/public/smf-overview': {
      get: {
        summary: 'SMF Overview Statistics',
        description: 'High-level statistics for the SMF system including total payments, success rate, volume, and average settlement time.',
        operationId: 'getSmfOverview',
        tags: ['SMF Public'],
        responses: {
          '200': {
            description: 'SMF overview statistics',
            content: {
              'application/json': {
                schema: {
                  $ref: '#/components/schemas/SmfOverview',
                },
                example: {
                  ok: true,
                  stats: {
                    totalPayments: 15420,
                    successRate: 0.987,
                    totalVolumeUsd: 1250000,
                    avgSettlementMs: 2340,
                    activeFacilitators: 4,
                    supportedNetworks: ['eip155:8453', 'eip155:84532', 'solana:mainnet'],
                  },
                  timestamp: '2025-12-27T10:30:00.000Z',
                },
              },
            },
          },
        },
      },
    },

    // =========================================================================
    // PUBLIC FACILITATORS LEADERBOARD
    // =========================================================================
    '/api/public/smf-facilitators': {
      get: {
        summary: 'Facilitator Leaderboard',
        description: 'Ranked list of x402 facilitators with scores, metrics, and activity data from x402scan and Scattering.',
        operationId: 'getSmfFacilitators',
        tags: ['SMF Public'],
        parameters: [
          {
            name: 'network',
            in: 'query',
            description: 'Filter by network (CAIP-2 format, e.g., eip155:8453)',
            schema: { type: 'string' },
            example: 'eip155:8453',
          },
          {
            name: 'timeframe',
            in: 'query',
            description: 'Timeframe for metrics (1d, 7d, 30d)',
            schema: { type: 'string', enum: ['1d', '7d', '30d'], default: '1d' },
          },
        ],
        responses: {
          '200': {
            description: 'Facilitator rankings with scores and metrics',
            content: {
              'application/json': {
                schema: {
                  $ref: '#/components/schemas/FacilitatorLeaderboard',
                },
              },
            },
          },
        },
      },
    },

    // =========================================================================
    // NEXFLOW-NATIVE METRICS
    // =========================================================================
    '/api/public/smf-nexflow-metrics': {
      get: {
        summary: 'NexFlow-Native Metrics',
        description: 'Aggregated metrics from NexFlow\'s own traffic (dogfood, scout, external agents). Complements x402scan and Scattering data.',
        operationId: 'getSmfNexflowMetrics',
        tags: ['SMF Public'],
        parameters: [
          {
            name: 'facilitatorId',
            in: 'query',
            description: 'Filter by facilitator ID',
            schema: { type: 'string' },
          },
          {
            name: 'network',
            in: 'query',
            description: 'Filter by network',
            schema: { type: 'string' },
          },
          {
            name: 'timeframe',
            in: 'query',
            description: 'Timeframe for aggregation (1h, 6h, 1d, 7d)',
            schema: { type: 'string', enum: ['1h', '6h', '1d', '7d'], default: '1d' },
          },
        ],
        responses: {
          '200': {
            description: 'NexFlow-native metrics by facilitator and network',
            content: {
              'application/json': {
                schema: {
                  $ref: '#/components/schemas/NexflowMetrics',
                },
              },
            },
          },
        },
      },
    },

    // =========================================================================
    // SMF SPEC (THIS ENDPOINT)
    // =========================================================================
    '/api/smf/spec': {
      get: {
        summary: 'SMF API Specification',
        description: 'Returns this OpenAPI specification document describing all public SMF endpoints.',
        operationId: 'getSmfSpec',
        tags: ['SMF Meta'],
        responses: {
          '200': {
            description: 'OpenAPI 3.0 specification document',
            content: {
              'application/json': {
                schema: {
                  type: 'object',
                  description: 'OpenAPI 3.0 document',
                },
              },
            },
          },
        },
      },
    },
  },

  // ===========================================================================
  // COMPONENT SCHEMAS
  // ===========================================================================
  components: {
    schemas: {
      // -----------------------------------------------------------------------
      // SMF Overview
      // -----------------------------------------------------------------------
      SmfOverview: {
        type: 'object',
        properties: {
          ok: { type: 'boolean' },
          stats: {
            type: 'object',
            properties: {
              totalPayments: { type: 'integer', description: 'Total payments processed' },
              successRate: { type: 'number', description: 'Success rate (0-1)' },
              totalVolumeUsd: { type: 'number', description: 'Total volume in USD' },
              avgSettlementMs: { type: 'number', description: 'Average settlement time in ms' },
              activeFacilitators: { type: 'integer', description: 'Number of active facilitators' },
              supportedNetworks: {
                type: 'array',
                items: { type: 'string' },
                description: 'List of supported networks (CAIP-2)',
              },
            },
          },
          timestamp: { type: 'string', format: 'date-time' },
        },
      },

      // -----------------------------------------------------------------------
      // Facilitator Leaderboard
      // -----------------------------------------------------------------------
      FacilitatorLeaderboard: {
        type: 'object',
        properties: {
          ok: { type: 'boolean' },
          network: { type: 'string', nullable: true },
          timeframe: { type: 'string' },
          recommended: { type: 'string', nullable: true, description: 'Recommended facilitator ID' },
          facilitators: {
            type: 'array',
            items: { $ref: '#/components/schemas/FacilitatorEntry' },
          },
          timestamp: { type: 'string', format: 'date-time' },
        },
      },

      FacilitatorEntry: {
        type: 'object',
        properties: {
          id: { type: 'string', description: 'Facilitator ID (e.g., cdp, payai)' },
          name: { type: 'string', description: 'Display name' },
          score: { type: 'number', description: 'Composite score (0-100)' },
          rank: { type: 'integer', description: 'Rank position' },
          selected: { type: 'boolean', description: 'Is this the recommended facilitator' },
          confidence: {
            type: 'string',
            enum: ['high', 'medium', 'low', 'none'],
            description: 'Data confidence level',
          },
          x402scan: {
            type: 'object',
            nullable: true,
            properties: {
              successRate: { type: 'number' },
              p95LatencyMs: { type: 'number' },
              invocations: { type: 'integer' },
              dataAgeHours: { type: 'number' },
            },
          },
          scattering: {
            type: 'object',
            nullable: true,
            properties: {
              volumeUsd3d: { type: 'number' },
              txCount3d: { type: 'integer' },
              uniqueBuyers3d: { type: 'integer' },
              activityScore: { type: 'number', description: '0-1 normalized activity score' },
            },
          },
          reasons: {
            type: 'array',
            items: { type: 'string' },
            description: 'Scoring reasons array (e.g., ["priority:1", "success-rate:100.0%"])',
          },
        },
      },

      // -----------------------------------------------------------------------
      // NexFlow-Native Metrics
      // -----------------------------------------------------------------------
      NexflowMetrics: {
        type: 'object',
        properties: {
          ok: { type: 'boolean' },
          timeframe: { type: 'string' },
          metrics: {
            type: 'array',
            items: { $ref: '#/components/schemas/NexflowFacilitatorMetrics' },
          },
          timestamp: { type: 'string', format: 'date-time' },
        },
      },

      NexflowFacilitatorMetrics: {
        type: 'object',
        properties: {
          facilitatorId: { type: 'string' },
          network: { type: 'string' },
          successRateNexflow: { type: 'number', description: 'Success rate from NexFlow traffic' },
          p95LatencyNexflow: { type: 'number', description: 'p95 latency from NexFlow traffic (ms)' },
          invocationsNexflow: { type: 'integer', description: 'Total NexFlow invocations' },
          byAgentType: {
            type: 'object',
            properties: {
              dogfood: {
                type: 'object',
                properties: {
                  invocations: { type: 'integer' },
                  successRate: { type: 'number' },
                },
              },
              scout: {
                type: 'object',
                properties: {
                  invocations: { type: 'integer' },
                  successRate: { type: 'number' },
                },
              },
              external: {
                type: 'object',
                properties: {
                  invocations: { type: 'integer' },
                  successRate: { type: 'number' },
                },
              },
            },
          },
        },
      },
    },
  },

  tags: [
    {
      name: 'SMF Public',
      description: 'Public endpoints for SMF data consumption',
    },
    {
      name: 'SMF Meta',
      description: 'Meta endpoints (spec, health, etc.)',
    },
  ],
} as const;

/**
 * Get the SMF spec as a plain JSON object
 */
export function getSmfSpec(): typeof SMF_SPEC {
  return SMF_SPEC;
}

/**
 * SMF Spec version for changelog tracking
 */
export const SMF_SPEC_VERSION = '1.0.0';

/**
 * Supported public endpoints list (for quick reference)
 */
export const SMF_PUBLIC_ENDPOINTS = [
  '/api/public/smf-overview',
  '/api/public/smf-facilitators',
  '/api/public/smf-nexflow-metrics',
  '/api/smf/spec',
] as const;

