// =============================================================================
// SMF SPEC API ROUTE
// =============================================================================
// Returns the machine-readable SMF API specification (OpenAPI 3.0)
//
// GET /api/smf/spec
//
// Example:
//   curl https://api.nexflowapp.app/api/smf/spec
//   curl https://www.nexflowapp.app/api/smf/spec
//
// =============================================================================

import { NextResponse } from 'next/server';
import { getSmfSpec, SMF_SPEC_VERSION } from '@/spec/smf-spec';

export const dynamic = 'force-static';
export const revalidate = 3600; // Cache for 1 hour

/**
 * GET /api/smf/spec
 * 
 * Returns the SMF public API specification as JSON (OpenAPI 3.0 format)
 */
export async function GET(): Promise<NextResponse> {
  const spec = getSmfSpec();

  return NextResponse.json(spec, {
    status: 200,
    headers: {
      'Content-Type': 'application/json',
      'Cache-Control': 'public, max-age=3600, s-maxage=3600',
      'X-SMF-Spec-Version': SMF_SPEC_VERSION,
      // CORS headers for external integrators
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET, OPTIONS',
      'Access-Control-Allow-Headers': 'Content-Type',
    },
  });
}

/**
 * OPTIONS /api/smf/spec
 * 
 * CORS preflight handler
 */
export async function OPTIONS(): Promise<NextResponse> {
  return new NextResponse(null, {
    status: 204,
    headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET, OPTIONS',
      'Access-Control-Allow-Headers': 'Content-Type',
      'Access-Control-Max-Age': '86400',
    },
  });
}

